import * as React from 'react'
import { Link } from 'gatsby'
import { container, gitIcon, aboutIcon, blogIcon, portfolioIcon, centerNav} from './centernav.module.css'
import { StaticImage } from "gatsby-plugin-image"
import TransitionLink from "gatsby-plugin-transition-link"
import AniLink from "gatsby-plugin-transition-link/AniLink"

const CenterNav = () => {
    return(
        <div className={container}>
            <div className={`${aboutIcon} ${centerNav}`}>
                <AniLink fade to="about" >
                    About
                </AniLink>
            </div>
            <div className={`${gitIcon} ${centerNav}` }>
                <Link to="https://github.com/gfreitag">
                    My Github
                </Link>
            </div>
            <div className={`${blogIcon} ${centerNav}`}>
                <AniLink swipe direction="up" top="exit" to="/blog" entryOffset={100} duration={1}>
                    Blog
                </AniLink>
            </div>
            <div className={`${portfolioIcon} ${centerNav}`}>
                <AniLink swipe direction="up" top="exit" to="/portfolio" entryOffset={100} duration={1}>
                    Portfolio
                </AniLink>
            </div>
        </div>
    )
}

export default CenterNav